<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process127"></a>

<p><br></p>
<p><span class="rvts17">嚎叫者（</span><span class="rvts50">The Shriekers</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">主线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:566,Y:276</span><span class="rvts21">，敌人：嚎叫者</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">破除嚎叫者身上的源力，进入港口。</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1044.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">港口大门有嚎叫者驻守，靠近嚎叫者就会使角色瞬间死亡。因此，必须找到破解嚎叫者的办法。破解嚎叫者的方法有两种，一种是寻找一种附带</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">技能的净化魔杖。这种魔杖只有两根。</span>
<br><span class="rvts21">第一根在</span><span class="rvts20"></span><span class="rvts21">布拉克斯</span><span class="rvts20"></span><span class="rvts21">雷克斯藏宝室</span><span class="rvts20"></span><span class="rvts21">支线任务中的那个山洞尽头的宝藏堆处。另一根在巫女雷迪卡（</span><span class="rvts20">Radeka</span><span class="rvts21">）手中，详见</span><span class="rvts20"></span><span class="rvts21">被净化的龙人</span><span class="rvts20"></span><span class="rvts21">支线任务。理论上来说，一根魔杖拥有</span><span class="rvts20">4</span><span class="rvts21">点</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">的充能，就足够破解大门处的</span><span class="rvts20">3</span><span class="rvts21">个嚎叫者。</span>
<br><span class="rvts21">然而，如果操作失误的话，需要把魔杖带到阿玛蒂亚避难所，找到格拉泰纳（</span><span class="rvts20">Gratiana</span><span class="rvts21">）进行充能。格拉泰纳在进入到地图东北角的废旧营地后会被召集在那里，而不再呆在阿玛蒂亚避难所。需要注意的是，如果打碎格拉泰纳的灵柩罐，便会使格拉泰纳死亡，详情参考</span><span class="rvts20"></span><span class="rvts21">永恒的崇拜者</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts21">另外一种方法就是可以通过找到军械库中的暴君头盔，清除嚎叫者。暴君头盔有</span><span class="rvts20">3</span><span class="rvts21">次</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">充能。但是使用完这</span><span class="rvts20">3</span><span class="rvts21">次充能后，会召唤出心中的恶魔。（有副作用。。。）（详见</span><span class="rvts20"></span><span class="rvts21">军械库</span><span class="rvts20"></span><span class="rvts21">支线和</span><span class="rvts20"></span><span class="rvts21">暴君套装</span><span class="rvts20"></span><span class="rvts21">支线下篇）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



